body {
    background-color: #262626;
    margin: 0;
    font-family: Helvetica, sans-serif;
    background-color: #282c34;
}

.App {
    text-align: center;
    max-width: 100%;
    margin: 0 auto;
}

.App header {
    background-color: rgb(80, 80, 80);
    height: 10vh;
    min-height: 50px;
    color: white;
    position: fixed;
    width: 100%;
    max-width: 100%;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 99;
    padding: 10px;
    box-sizing: border-box;
    box-shadow: 5px 5px 30px #2b2b2b;
}
.App section {
    justify-content: end;
    min-height: 100vh;
    height: auto;
    width: 100%;
    background-color: #36393f;
    padding-bottom: 80px;
    margin: 5%;
    overflow: hidden;
}
.App section,
main {
    width: 100%;
    margin: 0;
    align-self: center;
    display: flex;
    flex-direction: column;
    overflow: hidden;
}
main {
    height: auto;
    position: relative;
}
form {
    height: 60px;
    position: fixed;
    bottom: 0;
    width: 100%;
    max-width: 100%;
    display: flex;
    font-size: 1.5rem;
    margin-bottom: 2.5%;
    margin-left: 3%;
    margin-right: 3%;
}

input {
    display: flex;
    flex-direction: row;
    line-height: 0.5;
    width: 100%;
    font-size: 1.2rem;
    background: rgb(80, 80, 80);
    color: white;
    height: 60px;
    outline: none;
    border: none;
    border-radius: 10px;
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
    padding: 0 10px;
    box-shadow: 5px 5px 30px #2b2b2b;
}

button {
    background-color: rgb(80, 80, 80);
    border: none;
    height: 50px;
    color: #d13681;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    cursor: pointer;
    font-size: 1.25rem;
}

button:disabled {
    color: #000000c9;
    cursor: not-allowed;
}


.sign-in {
    background-color: rgb(80, 80, 80);
    border: none;
    height: 50px;
    color: #d13681;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    cursor: pointer;
    font-size: 1.25rem;
}


.sendbtn {
    background: rgb(80, 80, 80);
    border: none;
    color: #d13681;
    height: 60px;
    text-align: center;
    text-decoration: none;
    padding-right: 40px;
    display: inline-block;
    cursor: pointer;
    font-size: 1.25rem;
    border-radius: 0;
    margin-right: 8%;
    border-bottom-right-radius: 10px;
    border-top-right-radius: 10px;
    box-shadow: 5px 5px 30px -6px #2b2b2b;
}


ul,
li {
    text-align: left;
    list-style: none;
}

p {
    word-wrap: break-word;
    overflow-wrap: break-word;

    max-width: 500px;
    height: 13px;
    margin: 5px;
    line-height: 15px;
    padding: 15px;
    position: relative;
    color: white;
    text-align: center;
}

.message {
    display: flex;
    align-items: center;
    line-height: 1.4;
}


.sent {
    flex-direction: row-reverse;
}

.sent p {
    color: rgb(255, 255, 255);
    background-color: rgb(80, 80, 80);
    border-top-right-radius: 40px;
    border-top-left-radius: 40px;
    border-bottom-right-radius: 1px;
    border-bottom-left-radius: 40px;
    align-self: flex-end;
}
.received p {
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
    border-top-right-radius: 40px;
    border-top-left-radius: 40px;
    border-bottom-right-radius: 40px;
    border-bottom-left-radius: 1px;
}
img {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    margin: 5px;
}
.username {
    text-transform: capitalize;
    line-height: 0;
    align-self: flex-end;
    text-align: left;
}
